/* eslint-disable import/prefer-default-export */
import { getDefaultOptions, request } from 'api/helpers';

const resource = 'kie';

export const apiProcessModelsGet = async serviceUrl => {
  const url = `${serviceUrl}/${resource}/processModels`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};
